/* Container for the Navbar at the bottom */
.navbar-container {
  position: fixed;
  bottom: 0; /* Pin it to the bottom of the viewport */
  width: 100%; /* Stretch the navbar across the entire width */
  background-color: rgba(0, 0, 0, 0.8); /* Slightly transparent black background */
  padding: 15px 0; /* Thicker bar */
  z-index: 10; /* Ensure it stays above other content */
}

/* Navbar fade-in animation */
.fadeIn-animation {
  animation: fadeIn 2s forwards;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-list {
  display: flex;
  list-style: none;
  gap: 50px; /* Increased spacing between links */
  padding: 0;
  margin: 0;
}

.navbar-list li {
  text-transform: uppercase;
}

.navbar-list a {
  text-decoration: none;
  color: white;
  font-size: 20px; /* Larger text size */
  transition: color 0.3s ease-in-out;
}

.navbar-list a:hover {
  color: #f0a500; /* Add hover effect for links */
}

/* Keyframes for fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
